.project_card {
    padding: 1em;
    border: 1px solid #7a7a7a;
    border-radius: 5px;
    width: 24%;
    margin: 0.5%;

    & h4 {
        background-color: #002147;
        color: #FFBB33;
        padding: .4em;
        margin-bottom: 1.3em;
        font-size: 1.3em;
    }

    & p {
        color: #7a7a7a;
        margin-bottom: 1em;

        & span {
            font-weight: bold;
        }
    }
}

.category_text {
    display: flex;
    align-items: center;

    & span {
        display: block;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: #ccc;
        margin-right: 5px;
    }

    & .infra {
        background-color: #ffaebc;
    }

    & .desenvolvimento {
        background-color: #a0e7e5;
    }

    & .design {
        background-color: #b4f8c8;
    }

    & .planejamento {
        background-color: #fbe7c6;
    }
}

.project_card_actions {
    margin-top: 1.2em;
    display: flex;
    align-items: center;

    & svg {
        margin-right: .5em;
    }

    & a,
    button {
        text-decoration: none;
        border: none;
        background-color: #FFF;
        color: #002147;
        font-size: .9em;
        padding: .6em 1em;
        margin-right: 1em;
        cursor: pointer;
        border: 1px solid #002147;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.5s;

        &:hover {
            background-color: #002147;
            color: #FFBB33;
        }
    }
}