.home_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;

    & h1 {
        font-size: 2.5em;
        margin-bottom: .5em;

        & span {
            color: #FFBB33;
            padding: 0 .2em;
            background-color: #002147;
        }
    }

    & p {
        margin-bottom: 1.5em;
        color: #7b7b7b;
    }

    & img {
        width: 350px;
        margin: 2em 0;
    }

}